.console-output {
  background: var(--background);
  color: var(--text-main);
  font-family: Monaco, Consolas, "Lucida Console", monospace;
  padding: 1em;
}
.console-output p {
  margin: 0;
}
.console-output p.left {
  text-align: left;
}
.console-output p.right {
  text-align: right;
}
.console-output p.center {
  text-align: center;
}